@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap");

@font-face {
  font-family: "market_decoregular";
  src: url("font/market_deco-webfont.woff2") format("woff2"),
    url("font/market_deco-webfont.woff") format("woff"),
    url("font/market_deco.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}

/* display for mobile and tablet */
@media (max-aspect-ratio: 4/5) and (orientation: portrait) {
  html {
    font-size: 14px;
  }

  body .enter img {
    height: auto;
    width: 90%;
  }

  body .zizi-sidebar {
    left: 0;
    right: 0;
    top: auto;
    bottom: 0;
    height: 30rem;
    flex-direction: column-reverse;
    transition: bottom 1s ease;
  }

  body .zizi-sidebar .main-sidebar {
    width: 100%;
  }

  body .zizi-sidebar.closed {
    left: 0;
    bottom: -26rem;
  }

  body .zizi-sidebar .mini-sidebar {
    flex-direction: row;
  }

  body .zizi-sidebar .mini-sidebar .top-buttons,
  body .zizi-sidebar .mini-sidebar .centered-buttons {
    flex-direction: row;
  }

  body .main-bar-content {
    flex-shrink: 0;
    margin-top: 2rem;
  }

  body .zizi-sidebar .mini-sidebar .centered-buttons > a {
    padding: 1rem 20px 1rem 20px;
  }

  body .close-sidebar-left {
    height: 15px;
    width: 100%;
  }

  body .close-sidebar-left .close-button2 {
    display: inherit;
    padding-left: 1rem;
  }

  body .secondary-sidebar {
    left: 0;
    right: 0;
    top: auto;
    bottom: 0;
    height: 30rem;
    flex-direction: column;
    width: 100%;
    transition: bottom 1s ease;
  }

  body .secondary-sidebar .content-sidebar {
    padding-top: 0;
  }

  body .secondary-sidebar.closed {
    bottom: -31rem;
  }

  body .main-bar-content div:after {
    margin: 2rem 25% 2rem 25%;
  }

  body #main-bar-logo {
    order: 1;
    height: 8rem;
    flex-shrink: 0;
  }

  body .copyright {
    order: 2;
    margin-bottom: 1rem;
  }

  body .close-button2 {
    font-size: 2.1rem;
    padding: 1rem;
    background-color: #520319;
  }
}

body {
  background-color: black;
  color: white;
  font-family: "market_decoregular", sans-serif;
  z-index: 1;
  overflow: hidden;
  touch-action: none;
}

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.enter {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  top: 20%;
}

.enter img {
  height: 25rem;
}

.enter img:hover {
  -webkit-filter: drop-shadow(0px 0px 0px #800a12);
  filter: drop-shadow(0px 0px 0px #800a12);
}

.enter-screen button {
  position: absolute;
  bottom: 2rem;
  right: 6rem;
  font-size: 1.6rem;
  color: #ff9c84;
  background-color: rgba(46, 1, 15, 0.5);
  border-radius: 12px;
  padding: 0.6rem;
  filter: drop-shadow(20px 20px 5px #000);
}
.enter-screen button:hover {
  filter: brightness(160%);
}

.enter-screen button svg {
  top: 0.125em;
  position: relative;
}

.enter-screen .password {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 60%;
  font-size: 0.9rem;
  padding: 0.4rem;
  text-align: center;
  filter: brightness(40%) sepia(100%) hue-rotate(-45deg);
}
.enter-screen p {
  margin-top: 3rem;
}
.enter-screen b {
  font-size: 1.5rem;
  line-height:50px;
}


.zizi-intro-video {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
}

.intro-video {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -2;
}

.about-page {
  overflow: scroll;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

h1 {
  font-family: "market_decoregular", sans-serif;
  padding-bottom: 1rem;
  text-transform: uppercase;
  font-size: 2rem;
}

.zizi-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  background-color: #2e010f;
  background: linear-gradient(
    0deg,
    rgba(7, 0, 2, 1) 2%,
    rgba(46, 1, 15, 1) 70%,
    rgba(46, 1, 15, 1) 100%
  );
  color: #ff9c84;
  transition: left 1s ease;
}

.zizi-sidebar.closed {
  left: -27rem;
}

.zizi-sidebar a:hover {
  color: #b22d3f;
}

#main-bar-logo {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
  /* -webkit-flex: 1 0;
  flex: 1 0; */
}

.button-sidebar {
  box-shadow: 0px 0px 41px 10px rgba(0, 0, 0, 0.7);
  background-color: #380011;
  /* padding: 1rem; */
}

.mini-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.button-sidebar .zizi-icon {
  font-size: 2.1rem;
  height: 2rem;
}

.mini-sidebar .centered-buttons,
.mini-sidebar .top-buttons {
  display: flex;
  flex-direction: column;
}

.mini-sidebar .top-buttons {
  padding: 1rem 1rem 1rem 1rem;
  background-color: #520319;
}

.mini-sidebar .centered-buttons > a {
  padding: 20px 1rem 20px 1rem;
}

.close-sidebar-left {
  box-shadow: 0px 0px 41px 10px rgba(0, 0, 0, 0.7);
  background-color: #380011;
  width: 1rem;
  font-size: 2.1rem;
  z-index: 1;
}

.close-button {
  height: 2rem;
  font-size: 2.1rem;
  padding: 1rem;
  display: inline-flex;
  background-color: #520319;
}

.close-button2 {
  font-size: 2.1rem;
  /* padding: 1rem 1rem 1rem 0;
  margin-left: 1rem; */
  padding: 1rem;
  width: 30px;
  height: 30px;
  display: inline-flex;
  background-color: #520319;
}

.main-sidebar {
  width: 27rem;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  overflow: scroll;
}

.main-sidebar .zizi-icon {
  font-size: 2rem;
}

.main-sidebar a.inline-link {
  text-decoration: none;
  color: #ffbcaf;
}
.main-sidebar a.inline-link:hover {
  text-decoration: none;
  color: #d63a00;
}

.main-bar-content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.main-bar-content > div {
  padding-left: 1rem;
  padding-right: 1rem;
}

.main-bar-content .player-controls a {
  margin: 0 1rem 0 1rem;
}

.main-bar-content div:after {
  content: "";
  width: 6rem;
  height: 1px;
  background-color: #ffbcaf;
  display: block;
  margin: 3rem auto 3rem auto;
}

.copyright {
  margin-bottom: 5rem;
}

.main-sidebar sub {
  display: block;
}

.main-sidebar .now-playing p {
  margin: 0;
  font-size: 1.1rem;
}

.zizi-sidebar .main-bar-content .sidebar-large-button {
  font-size: 2rem;
}

.secondary-sidebar {
  position: absolute;
  top: 0;
  right: 0;
  width: 30rem;
  height: 100%;
  display: flex;
  background-color: #2e010f;
  /* background: linear-gradient(0deg, rgba(7,0,2,1) 2%, rgba(46,1,15,0.5) 70%, rgba(46,1,15,0.9) 100%); */
  background: linear-gradient(
    0deg,
    rgba(7, 0, 2, 1) 2%,
    rgba(46, 1, 15, 1) 70%,
    rgba(46, 1, 15, 1) 100%
  );
  color: #ff9c84;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  transition: right 1s ease;
}

.secondary-sidebar.closed {
  right: -30rem;
}

.secondary-sidebar .content-sidebar {
  overflow: scroll;
  width: 100%;
}

.performers-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  line-height: 0.2rem;
  font-size: 1.2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

.performers-list .performer {
  padding-bottom: 0.8rem;
}

.performers-list a:hover {
  /* box-shadow: 0px 0px 50px 4px rgba(0,0,0,1); */
}

.performers-list .info a {
  font-size: 0.9rem;
  text-decoration: none;
  color: #a33122;
}

.performers-list .info a:hover {
  color: #9b002e;
}

.bios h1 {
  font-size: 2rem;
}

.bios .backbutton {
  text-align: right;
  font-size: 1.5rem;
  background-color: #9c0031;
  padding: 0.5rem;
}

.secondary-header {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  text-align: center;
  margin-top: 2rem;
}

.secondary-sidebar .performer img {
  height: 10rem;
  box-shadow: 4px 4px 10px 1px rgba(0, 0, 0, 0.5);
  margin: 0.4rem;
}

.secondary-sidebar .performer img:hover {
  filter: sepia(100%);
  /* filter: contrast(90%) brightness(123%) blur(2px); */
  /* https://developer.mozilla.org/en-US/docs/Web/CSS/filter */
}

.secondary-sidebar .song img {
  height: 4rem;
  float: left;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}

.secondary-sidebar .song {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  text-align: left;
  margin-bottom: 1rem;
  /* background-color: #38001170; */
}

.secondary-sidebar .song:hover {
  box-shadow: 0px 0px 50px 5px rgba(0, 0, 0, 0.7);
}

.secondary-sidebar .song p {
  height: 4rem;
  font-size: 0.85rem;
  padding-left: 1rem;
  width: 200px;
  display: table-cell;
  vertical-align: middle;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.secondary-sidebar .song w {
  color: #fff;
}
.secondary-sidebar .song:hover w {
  color: #d63a00;
}
.secondary-sidebar .song a:hover {
  color: #f5c5b3;
}
.secondary-sidebar .song .artist {
  color: #edb49f;
}
.secondary-sidebar .song .artist:hover {
  color: #d63a00;
}

h2 {
  font-family: "market_decoregular", sans-serif;
  padding-bottom: 15px;
  text-transform: uppercase;
  font-size: 2rem;
  text-align: center;
}

.secondary-sidebar .about {
  width: 100%;
  height: 300%;
  background-color: #000;
  margin: 1.5rem;
  font-family: "Lato";
  float: left;
  text-align: left;
  font-size: 15px;
  font-size: 0.85rem;
}

.secondary-sidebar .content-sidebar .about {
  padding-top: 0px;
}

.secondary-sidebar .about p {
  margin: 1.5rem;
  margin-top: 0rem;
}

.secondary-sidebar .about img {
  width: 100%;
}

.curtain-bg {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  user-select: none;
  transform: translate(-50%, -50%);
  z-index: -4;
  transition: opacity 1s;
}

.curtain-side {
  position: absolute;
  height: 101%;
  left: 50%;
  top: 50%;
  user-select: none;
  transform: translate(-50%, -50%);
  /* mix-blend-mode: overlay; */
  z-index: -1;
}

.video-frame {
  position: absolute;
  height: 100%;
  width: 100vh;
  left: 50%;
  right: 50%;
  overflow: hidden;
  transform: translateX(-50%);
}

.drag-video {
  height: 100%;
  user-select: none;
}

.drag-video.zoom {
  transform: translateX(-50%);
}

.primary-player {
  mix-blend-mode: lighten;
  z-index: -2;
}

.shadow-player {
  mix-blend-mode: multiply;
  z-index: -3;
}

/* unvisited link */
a:link {
  color: #d98b6f;
  cursor: pointer;
}

/* visited link */
a:visited {
  color: #edb49f;
  cursor: pointer;
}

/* mouse over link */
a:hover {
  color: #aa272e;
  cursor: pointer;
}

/* selected link */
a:active {
  color: #fff;
  cursor: pointer;
}

a:disabled {
  color: #555;
}

.about-page {
  color: #ee9c92;
  font-family: 'Roboto Slab', charter, Georgia, Cambria, "Times New Roman", Times, serif;
  line-height: 32px;
  font-size: 18px;
  margin: 0px;
  z-index: -4;
  padding: 1rem;
}

/* about page */
.about-page h2 {
  font-family: "market_decoregular", sans-serif;
  margin-top: 4rem;
  text-align: center;
}

.about-page .about {
  max-width: 600px;
  margin: auto;
  display: block;
  object-fit: fill;
}

.about-page .secondary-header {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
  width: 20rem;
}


.about-page .banner {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  max-width: 700px;
  mix-blend-mode: lighten;
}


.about-page .first {
  margin-top: 0;
}

.about-page hr {
  border-color: #ee9c92;
  margin: 4rem;
}

.about-page hr .first {
  margin-top: 0;
}

.about-page .footnote {
  font-size: 17px;
}

.about-page sup {
  font-size: 12px;
}

.about-page .curtain-bg {
  filter: brightness(40%);
}
.about-page .close {
  font-size: 3rem;
}

.about-page .diagram {
  width: 100%;
  margin: 20px 0 20px 0;
}